
.feedback {
  padding-top: 40px;
  padding-bottom: 55px;
}

.feedback__form {
  display: flex;
  flex-direction: column;
}
.feedback__form .main__title {
  text-align: center;
}

.feedback__item {
  border: 1px solid black;
  padding: 15px;
  font-family: 'Arial', 'Helvetica', sans-serif;
  font-size: 15px;
  color: black;
  margin-bottom: 15px;
  opacity: 1;

  &::placeholder {
    font-family: 'Arial', 'Helvetica', sans-serif;
    font-size: 15px;
    color: black;
  }

  &:focus::placeholder {
    opacity: 0;
    transition: opacity 0.3s;
  }

  &:focus {
    box-shadow: 1px 1px 1px black, -1px -1px 1px  black;
  }

  &--message {
    resize: none;
    min-height: 150px;
  }
}

.feedback__button {
  background: white;
  max-width: 100px;
  align-self: center;
}

.feedback__form .error {
  border-color: #950f1f;
  box-shadow: 1px 1px 0 0 #950f1f, -1px -1px 0 0 #950f1f;

  &:hover {
    border-color: #950f1f;
    box-shadow: 1px 1px 0 0 #950f1f, -1px -1px 0 0 #950f1f;
  }

  &:focus {
    border-color: #950f1f;
    box-shadow: 1px 1px 0 0 #950f1f, -1px -1px 0 0 #950f1f;
  }
}
