.certificates {
  padding-top: 40px;
  padding-left: 30px;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  & .main__title {
    width: 100%;
  }
}

.certificates__item {
  width: 250px;
  height: 355px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-left: 10px;

  &:hover {
    cursor: pointer;
  }
}

.certificates__img {
  width: 100%;
  height: 100%;
}

@media (max-width: 600px) {
  .certificates__item {
    width: 100%;
    height: auto;
    margin-right: 0;
    margin-left: 0;
  }
}
