$animationSpeed: 120s;

.slider {
  padding-top: 40px;
  padding-bottom: 55px;
  overflow: hidden;
  width: 100%;

  & .main__title {
    text-align: center;
  }
}

.slider-track {
  padding-left: 0;
  list-style: none;
  animation: scroll $animationSpeed linear infinite;
  display: flex;
  width: calc(200px * 28);
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-200px * 14));
  }
}

.slider-item {
  height: 200px;
  width: 200px;
  margin-right: 20px;
  margin-left: 20px;
  display: flex;
  align-items: center;
}


.slider-item img {
  width: 100%;
  height: auto;
  filter: grayscale(100%);
}

.slider-item p {
  text-align: center;
}

.slider-item:hover img {
  filter: none;
}
