html {
  position: relative;
}

.zoom {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: none;
  justify-content: center;
  align-items: flex-start;
}

.zoom__img-wrapper {
  margin-top: 55px;
  flex-shrink: 0;
  position: fixed;
}

.zoom__img {
  height: 100%;
}

.zoom__button {
  position: absolute;
}

.zoom__button--close {
  width: 40px;
  height: 40px;
  background: transparent;
  right: -40px;
  top: -40px;
  border: none;
  padding: 0;

  &::before,
  &::after {
    display: block;
    position: absolute;
    top: 0;
    left: 20px;
    content: '';
    height: 40px;
    width: 4px;
    background-color: black;
    border-radius: 4px;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &::before {
    transform: rotate(45deg);
  }

  &:hover::after,
  &:hover::before {
    background: #950f1f;
  }

  &:hover {
    cursor: pointer;
  }
}

.zoom__button--right,
.zoom__button--left {
  width: 30px;
  height: 4px;
  background: black;
  top: calc(50% - 20px);
  border: none;
  padding: 0;
  border-radius: 4px;

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border: 10px solid transparent;
  }

  &:hover {
    cursor: pointer;
    background: #950f1f;
  }

  &:hover::after {
    border-left-color: #950f1f;
    border-right-color: #950f1f;
  }
}

.zoom__button--right {
  right: -40px;

  &::before,
  &::after {
    border-right: 0;
  }

  &::after {
    top: -8px;
    right: -9px;
    border-left-color: black;
  }
}

.zoom__button--left {
  left: -40px;

  &::before,
  &::after {
    border-left: 0;
  }

  &::after {
    top: -8px;
    left: -9px;
    border-right-color: black;
  }
}
