.header--main-title .header__wrapper {
  height: 525px;
  display: flex;
  flex-direction: column;
}

.header--main-title {
  background: url("../../img/building-desktop.jpg") no-repeat center top;
  background-size: cover;
  min-height: 525px;
}

@media (max-width: 1440px) {
  .header--main-title {
    background: url("../../img/building-desktop1440.jpg") no-repeat center top;
    background-size: cover;
  }
}

@media (max-width: 768px) {
  .header--main-title {
    background: url("../../img/building-desktop768.jpg") no-repeat center top;
    background-size: cover;
  }
}

@media (max-width: 320px) {
  .header--main-title {
    background: url("../../img/building-desktop320.jpg") no-repeat center top;
    background-size: cover;
  }
}

.header--main-title .header__top {
  background: transparent;
}

.header__title {
  display: block;
  box-sizing: border-box;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  margin-top: auto;
  font-size: 41px;
  color: white;
}

@media (max-width: 1040px) {
  .header__title.center {
    width: calc(100% - 60px);
    font-size: 31px;
  }
}


//мобильная кнопка меню

.visuallyHidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.hamburger {
  width: 30px;
  height: 30px;
  position: relative;
  padding-right: 30px;

  &:hover {
    cursor: pointer;
  }
}

.header--main-title .hamburger .bar {
  background-color: white;
}

.hamburger .bar {
  padding: 0;
  width: 30px;
  height: 2px;
  background-color: black;
  display: block;
  border-radius: 4px;
  transition: all 0.4s ease-in-out;
  position: absolute;
}

.bar1 {
  top: 0;
}

.bar2,
.bar3 {
  top: 10px;
}

.bar3 {
  right: 0;
}

.bar4 {
  bottom: 0;
}


.hamburger4 .bar4 {
  top: 10px;
}

.hamburger4 .bar5 {
  bottom: 6px;
}

.hamburger4 .bar {
  transition: all 0.4s ease-in-out, transform 0.4s ease-in-out 0.4s;
}

.hamburger4 .bar2 {
  width: 1px;
  transform: rotate(90deg);
  left: 13.5px;
}

.hamburger4 .bar3 {
  width: 1px;
  left: 13.5px;
}

.checkbox4:checked + label > .hamburger4 > .bar1 {
  top: 10px;
  background-color: transparent;
}

.checkbox4:checked + label > .hamburger4 > .bar2 {
  left: 0;
  width: 30px;
  transform: rotate(45deg);
}

.checkbox4:checked + label > .hamburger4 > .bar3 {
  left: 0;
  width: 30px;
  transform: rotate(-45deg);
}

.checkbox4:checked + label > .hamburger4 > .bar4 {
  background-color: transparent;
}

.checkbox4:checked + label > .hamburger4 > .bar5 {
  bottom: 10px;
  background-color: transparent;
}

.header__top {
  background: #f3f3f3;
  position: fixed;
  width: 100%;
  height: 100px;
  transition: all 0.8s ease;

  & .center {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    height: 100%;
  }
}

.header__top.scroll {
  background: white;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.75);
  height: 60px;
  z-index: 999;
}

.header__top.scroll .header__logo {
  width: auto;
  height: 20px;
}

.header__top.scroll .header__menu-button {
  top: 17px;
}

.header__top.scroll .header__menu-link {
  color: black;
}

.header__menu-list {
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
  flex-direction: row;
}

.header--main-title .header__menu-link {
  color: white;
}

.header__menu-link {
  text-transform: uppercase;
  text-decoration: none;
  color: black;
  text-align: left;
  font-size: 13px;
  font-weight: bold;
  margin-left: 15px;

  &[href]:hover {
    cursor: pointer;
  }
}

.header__menu-button {
  display: none;
}

.header__logo {
  width: auto;
  height: 80px;
  transition: height 0.8s ease;
}

.header--main-title .header__logo .text {
  fill: white;
}

.header--main-title .header__logo .p {
  fill: white;
}

.header--main-title .header__logo .first_c {
  fill: white;
}

.header--main-title .header__logo .last_c {
  fill: white;
}

.header__logo .text {
  fill: black;
}

.header__logo .p {
  fill: black;
}

.header__logo .first_c {
  fill: #220f42;
}

.header__logo .last_c {
  fill: #950f1f;
}

.header__top.scroll .header__logo {
  width: auto;
  height: 60px;
}

.header__top.scroll .header__logo .text {
  fill: black;
}

.header__top.scroll .header__logo .p {
  fill: black;
}

.header__top.scroll .header__logo .first_c {
  fill: #220f42;
}

.header__top.scroll .header__logo .last_c {
  fill: #950f1f;
}

.header__top.scroll .hamburger .bar {
  background-color: black;
}

@media (max-width: 890px) {
  .header__top {
    height: 60px;
  }

  .header__top .center {
    padding-right: 0;
    padding-left: 0;
  }

  .header__logo {
    width: auto;
    height: 60px;
  }

  .header__menu-list {
    position: absolute;
    top: 60px;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
    visibility: hidden;
  }

  .header--main-title .header__menu-link {
    color: black;
  }

  .header__menu-link {
    background: #eeeeee;
    border-bottom: 1px solid #e2e2e2;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 15px;
    margin-left: 0;
    color: black;
    display: block;
    transform: translateY(-900px);
    transition: all 0.8s ease;
  }

  .header__menu-button {
    display: block;
    position: absolute;
    top: 20px;
    right: 15px;
  }

  .header__menu-list.menu-open {
    visibility: visible;
  }

  .header__menu-list.menu-close {
    visibility: hidden;
  }

  .header__menu-list.menu-open .header__menu-link {
    transform: translateY(0);
    transition: all 0.8s ease;
  }

  .header__menu-list.menu-close .header__menu-link {
    transform: translateY(-900px);
    transition: all 0.8s ease;
  }
}

// стили для ie
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .header__menu-list {
    position: relative;
    margin-top: -65px;
  }
}
