.services {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-left: 30px;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 40px;
  padding-bottom: 55px;

  & .main__title {
    width: 100%;
    text-align: center;
  }
}

.services__item {
  box-shadow: 1px 1px 4px 0 rgba(0,0,0,0.6), -1px -1px 4px 0 rgba(0,0,0,0.6);
  flex-basis: 260px;
  flex-grow: 0;
  flex-shrink: 0;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 15px;

  &:hover {
    transform: scale(1.05);
  }
}

.services__title {
  text-transform: uppercase;
  font-size: 22px;
  text-align: center;
  width: 100%;
}
