.about-us {
  display: flex;
  justify-content: space-between;
}

.about-us__left {
  flex-basis: 50%;
  flex-shrink: 0;
  flex-grow: 1;
  background: #f3f3f3;
  display: flex;
  justify-content: flex-end;
}

.about-us__right {
  flex-basis: 50%;
  flex-shrink: 0;
  flex-grow: 1;
  background: url("../../img//tower.jpg") no-repeat;
  background-size: cover;
}

.about-us__content {
  max-width: 430px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 55px;
  padding-left: 15px;
}

.about-us__text {
  margin-top: 0;
  margin-bottom: 20px;
}

.about-us__button {
  background: #f3f3f3;
}

@media (max-width: 890px) {
  .about-us__right {
    display: none;
  }

  .about-us__left {
    justify-content: center;
  }

  .about-us__content {
    max-width: 100%;
  }
}
