html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  background: #ffffff;
  min-width: 320px;
  font-family: 'Arial', 'Helvetica', sans-serif;
  font-size: 15px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.center {
  width: 969px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 30px;
  padding-right: 30px;
}

@media (max-width: 1040px) {
  .center {
    width: auto;
  }
}

@media (max-width: 890px) {
  .center {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.button {
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  border: 2px solid black;
  min-width: 230px;
  padding-top: 10px;
  padding-bottom: 10px;
  text-decoration: none;
  color: black;

  &:hover {
    color: white;
    border-color: white;
    background: black;
    cursor: pointer;
  }
}

*:active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}
