.main {
  margin-top: 100px;
  min-height: 100vh;

  &--main-title {
    margin-top: 0;
  }
}

.main__title {
  font-family: 'Oswald', sans-serif;
  font-size: 38px;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 40px;
}

.main--staff {
  padding-top: 40px;
  padding-bottom: 55px;
}

@media (max-width: 600px) {
  .main__title {
    font-size: 23px;
  }
}
