.products {
  padding-bottom: 20px;
  padding-left: 30px;
  list-style: none;

  & .main__title {
    text-align: center;
  }
}

.products__title {
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 15px;
}

.products__description {
  border-bottom: 1px solid black;
  padding-bottom: 17px;
  margin-top: 0;
  font-size: 14px;
}

.products__item {
  margin-right: 60px;
  margin-left: 60px;
}

@media(max-width: 800px) {
  .products__item {
    margin-right: 0;
    margin-left: 0;
  }
}

.fadeIn {
  animation-name: fadeIn;
  animation-duration: 3s;
  animation-fill-mode: both;
}

$step: 0;

@for $i from 1 through 8 {
  .products__item:nth-of-type(#{$i}) {
    animation-delay: ($step + s);
    $step: 0.5*$i;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
